___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

img {
  display: block;
}

#restaurant-list {
  display: flex;
  flex-direction: column;
}

#restaurant-list__container {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.restaurant-list__title {
  font-size: 2rem;
  padding: 2rem;
  margin: 0 auto;
}

.skip-link {
  position: absolute;
  top: -50px;
  left: 50px;
  padding: 8px;
  background-color: #fff;
  color: #000;
  text-decoration: none;
  z-index: 100;
}

.skip-link:focus {
  top: 50px;
}
